import { SplideSlide } from "@splidejs/react-splide";
import React, { useEffect, useRef, useState } from "react";
import { Button, Card } from "react-bootstrap";
import SlideSection from "./common/slideSection";
import SectionHeading from "../../Analytics/layout/common/sectionHeading";
import AuthorInfo from "../../Analytics/content/components/authorInfo";
import NetworkIcon from "../../Analytics/layout/common/networkIcon";
import PostMedia from "../../Analytics/layout/common/postMedia";
import { useDispatch } from "react-redux";
import { postStatusDashboard } from "../../../../../actions/moderationActions";
import RecentpostSkeleton from "./skeleton/recentpostSkeleton";
import { useNavigate } from "react-router-dom";
import Sentiment from "./common/sentiment";

const RecentPost = (props) => {

    const splideRef = useRef(null);
    const dispatch = useDispatch()
    const navigate = useNavigate();
    const [updateSlider, setupdateSlider] = useState(0);
    const [activeWall, setactiveWall] = useState(0);
    const { recentPosts, loader, getNotRecomendedList, isUpgrade } = props

    const postStatus = (index, item, value) => { dispatch(postStatusDashboard(index, item, value)); }

    const handleResize = () => setupdateSlider(Math.random())

    const redirect = (e) => { navigate(`/moderation/index/${e}`); localStorage.setItem("wallId", e) }

    useEffect(() => {
        window.addEventListener("resize", handleResize);
        return () => {
            window.removeEventListener("resize", handleResize);
        };
    }, []);

    return (
        (recentPosts && Object.keys(recentPosts).length) || loader ?
            <div className='recent-post-section mb-4'>
                <Card className='p-0 card-custom card-stretch'>
                    <SectionHeading title={'Recent Posts'} total={0} section={2} isDashboard={'card-title h5 w-100'} />
                    <Card.Body className="py-3 px-0 ">
                        {loader ? <RecentpostSkeleton /> :
                            <SlideSection key={updateSlider} setactiveWall={setactiveWall} section={2} totalRecord={recentPosts.length} splideRef={splideRef}>
                                {recentPosts.map((e, index) => {
                                    return (
                                        <SplideSlide key={index}>
                                            <div onLoad={isUpgrade ? getNotRecomendedList(e) : null} className='p-3 d-flex h-100 cursor-pointer'>
                                                <div className='p-3 border rounded w-100'>
                                                    <div className='row h-100'>
                                                        <div key={index} onClick={() => redirect(e.wallId)} className='col-sm-5'>
                                                            <div className='home_recentPost text-center h-100'>
                                                                <PostMedia isSplide={true} postFile={e.postFile} content={e.content} mediaType={e.type} postId={e.id} className={"img-fluid"} />
                                                            </div>
                                                        </div>
                                                        <div className='col-sm-7'>
                                                            <div className='d-flex flex-column w-100 h-100 justify-content-between home_recentPost-content'>
                                                                <div key={index} onClick={() => redirect(e.wallId)} className="w-100">
                                                                    <div className='d-flex align-items-center justify-content-between mb-3 home_recentPost_auther'>
                                                                        <AuthorInfo isSplide={true} picture={e.newPicture} authorUsername={e.username} authorName={e.name} date={e.created} networkId={e.social.id} />
                                                                        <div className="list_category_icon cursor-pointer pt-1">
                                                                            <NetworkIcon isDashboard={'font-size-h2'} icon={e.social.icon} />
                                                                        </div>
                                                                    </div>
                                                                    <div className='mb-3'> <p className="line-clamp-3 overflow-hidden text-dark"> {e.content} </p> </div>
                                                                </div>
                                                                <div className="w-100">
                                                                    <div onClick={() => redirect(e.wallId)} className='mb-3'>
                                                                        {isUpgrade ?
                                                                            <div className="d-flex align-items-center justify-content-between p-2 mb-3 border rounded">
                                                                                <span className="text-dark font-weight-bold font-size-md">Sentiment</span>
                                                                                <p className="text-dark fw-bold mb-0 d-flex"> <Sentiment sentiment={e.sentiment} /> </p>
                                                                            </div> : null}
                                                                        <div className="d-flex align-items-center justify-content-between p-2 mb-3 border rounded">
                                                                            <span className="text-dark font-weight-bold font-size-md">Wall Name</span>
                                                                            <p className="text-dark font-weight-boldest mb-0 w-50 overflow-hidden text-nowrap text-ellipsis text-end">{e.wall.name}</p>
                                                                        </div>
                                                                    </div>
                                                                    <div className="m_bottom_actions border-0">
                                                                        <ul className="list-inline mb-0 d-flex p-0">
                                                                            <li className="w-50 me-2"> <Button variant='public' size='sm' id={`approveBtn${e.id}`} onClick={() => e.postStatus != 1 ? postStatus(index, e, 1) : null} className={`w-100 ${e.postStatus == 1 ? 'm_active disabled' : ''} init`}>Public</Button> </li>
                                                                            <li className="w-50"> <Button variant='private' size='sm' id={`rejectBtn${e.id}`} onClick={() => e.postStatus != 2 ? postStatus(index, e, 2) : null} className={`w-100  ${e.postStatus == 2 ? 'm_active disabled' : ''} init`}>Private</Button> </li>
                                                                        </ul>
                                                                    </div>
                                                                </div>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        </SplideSlide>
                                    )
                                })}
                            </SlideSection>
                        }
                    </Card.Body>
                </Card>
            </div> : null
    );
};

export default React.memo(RecentPost)