import React, { useEffect, useState } from "react";
import { Splide, SplideTrack } from "@splidejs/react-splide";
import "../../../Analytics/assets/css/slide.scss";
import "@splidejs/react-splide/css";
import { FontAwesomeIcon } from "../../../../../CommonComponents/AssetsHelper";
import { Button } from "react-bootstrap";

const CreatedWalls = ({ setactiveWall, section, totalRecord, splideRef, children, }) => {
  const [currentSlide, setCurrentSlide] = useState(0);
  const [perPage, setPerPage] = useState(); // Default perPage count


  useEffect(() => {

    if (section && setactiveWall) {
      const splideInstance = splideRef.current.splide;

      //   const handleResize = () => setPerPage(splideInstance.options.perPage);

      // Attach the resize event listener
      // splideInstance.on('resize', handleResize);
      // Set the initial perPage value
      setPerPage(splideInstance.options.perPage);

      splideInstance.on("click", (e) => {
        if(section==1) splideRef.current.splide.go(e.index); // Move to the clicked slide
      });

      splideRef.current?.splide.on("move", (newIndex) => {

          if(section==1){
            manageSlider(newIndex);
            setactiveWall(newIndex);
        }
          setCurrentSlide(newIndex);
        });

      if (section==1 && (!splideRef.current || totalRecord <= 1)) {
        manageSlider();
        setactiveWall(1);
      }
    } else {
      splideRef.current?.splide.on("move", (newIndex) => {
        setactiveWall(newIndex);
      });
    }
  }, []);

  const manageSlider = (index = null) => {
    const totalList = totalRecord * 2;
    const newIndexCondition = totalRecord - 1;

    const toggleDisplay = (displayStyle) => {
      const slideWalls = document.getElementsByClassName(`splide__slide_walls`);

      for (let i = totalRecord + 1; i <= totalList; i++) {
        if (slideWalls && slideWalls.length) {
          let updateKey = totalRecord - 1;
          let elementNew =
            slideWalls[i + updateKey] &&
            document.getElementById(slideWalls[i + updateKey].id);
          if (elementNew) elementNew.style.display = displayStyle;
        }
      }
    };

    if (index === newIndexCondition || index == null) {
      if (index == null) {
        const Ispreview = document.getElementsByClassName(`is-prev`);
        const splideListUl = document.getElementsByClassName(`splide__list_ul`);
        const Isnext = document.getElementsByClassName(`is-next`);

        if (Ispreview.length) Ispreview[0].style.display = "none";
        if (Isnext.length) Isnext[0].style.display = "none";
        if (splideListUl)
          splideListUl[0].querySelector("ul").style.transform = "translateX(0)";
      } else {
        toggleDisplay("none");
      }
    } else {
      toggleDisplay("block");
    }
  };

  const createBreakpointSettings = (perPage, isWall, clones, postCount) => ({ perPage, clones: clones, autoplay: isWall && postCount > perPage, drag: totalPost > 1 && postCount > perPage, loop: postCount > perPage, arrows: postCount > perPage, });

  const totalPost = totalRecord || 0;
  const canSlide = totalPost > 3;
  const perPagePost = canSlide ? 3 : totalPost;

  const settings = {
    start: 0,
    type: section == 1 ? "loop" : "slide",
    //rewind: true,
    speed: 1000,
    perMove: 1,
    loop: false,
    lazyLoad: 'nearby',
    interval: 5000,
    drag: totalPost > 1 ? true : false,
    //clones: 1,
    perPage: section == 1 ? 0 : totalRecord > 3 ? 3 : totalRecord,
    autoWidth: section == 1 ? true : false,
    gap: section == 1 ? 15 : -15,
    autoplay: section == 1 ? false : true,
    pagination: false,
    arrows: section === 1 ? totalRecord !== 1 : true,
    fixedHeight: true,
    breakpoints: {
      0: createBreakpointSettings(1, 1, perPagePost, totalPost),
      579: createBreakpointSettings(1, 1, perPagePost, totalPost),
      1300: createBreakpointSettings(section == 1 ? 1 : 2, 1, 0, totalPost),
      2200: createBreakpointSettings(section == 1 ? 1 : 3, section == 1 ? 0 : 1, 0, totalPost),
    },
  };

  return (
    <Splide className={section ? "splide__list_ul" : ""} ref={splideRef} hasTrack={false} options={settings} >
      <SplideTrack>{children}</SplideTrack>

      <div className="splide__arrows splide__arrows--ltr tb_g_h_arrow_wrapper_" role="navigation" aria-label="Slider Navigation Wrapper" >
        <div role="navigation" className="tb_g_h_arrow splide__arrow splide__arrow--prev tb_g_h_arrow_left__ tb__icon tb-arrow-left-alt" aria-labelledby="Navigation Previous Slide" >
          {" "}
          <Button variant="" disabled={currentSlide === 0}>
            <FontAwesomeIcon type={'solid'} icon={'chevron-left'}/>
          </Button>
        </div>
        <div role="navigation" className="tb_g_h_arrow splide__arrow splide__arrow--next tb_g_h_arrow_right__ tb__icon tb-arrow-right-alt" aria-labelledby="Navigation Next Slide" >
          {" "}
          <Button variant="" disabled={currentSlide === totalRecord - (section == 1 ? 1 : perPage)}> 
            <FontAwesomeIcon type={'solid'} icon={'chevron-right'}/>
            {" "}
          </Button>
        </div>
      </div>
    </Splide>
  );
};

export default CreatedWalls;