    import React from "react";
    import { SENTIMENTS_CALC } from "../../../../../../utils";
    import { MediaBaseUrl } from "../../../../../../actions/api";

    const Sentiment = (props) => {
    const { sentiment } = props;

    return (
        <>
        {sentiment > 0 ? (
            <div className="d-inline-flex post-status bg-position">
            <img className="img-fluid me-2" src={`${MediaBaseUrl}icons/emoji/emoji${ SENTIMENTS_CALC(sentiment).item }.svg`} width={"16px"} height={"16px"} />
            <span className="overflow-hidden text-nowrap text-ellipsis"> {SENTIMENTS_CALC(sentiment).label} </span>
            </div>
        ) : ( "-" )}
        </>
    );
    };

    export default React.memo(Sentiment);
