import React from "react";

const List = () => {
  return (
    <div className="mt-3 ex-recentwbox p-2">
        <div className="d-flex align-items-center justify-content-between w-100">
        <div className="sk_line sk_line_height_12 sk_line_30 m-0"></div>
        <div className="sk_line sk_line_height_12 sk_line_30 m-0"></div>
        </div>
    </div>
  );
};

const RecentpostSkeleton = () => {
  return (
    <div className="">
      <div className="d-flex align-items-center flex-wrap justify-content-between">
        {[1, 2, 3].map(() => {
          return (
            <div className="col-4 px-2">
              <div className="w-100">
                <div className="ex-recentwbox d-block p-4">
                  <div className="row">
                    <div className="col-4">
                      <div className="ex-recentwright w-100 mb-3">
                        <div
                          className="sk_rect btn-min-w sk_img_thumb"
                          style={{ paddingBottom: "55%" }}
                        ></div>
                      </div>
                    </div>
                    <div className="col-8">
                      <div className="w-100">
                        <div className="d-flex align-items-center flex-grow-1">
                          <div className="symbol symbol-45 symbol-light me-2 con_user_img">
                            <span className="sk_img"></span>
                          </div>
                          <div className="d-flex text-left flex-column conn_user_info w-50">
                            <div className="sk_line sk_line_height_12 mb-2"></div>
                            <div className="sk_line sk_line_50 sk_line_height_10 mt-2"></div>
                          </div>
                        </div>
                        <div className="mt-3">
                          <div className="mb-2">
                            <div className="d-flex text-left flex-column conn_user_info">
                              <div className="sk_line sk_line_height_12 w-100 m-0"></div>
                              <div className="sk_line sk_line_height_12 w-50"></div>
                            </div>
                          </div>
                        </div>

                        <List/>
                        <List/>
                        <List/>

                        <div className="ex-recentwright d-flex w-100">
                          <div className="sk_line sk_line_height_28 me-2 mb-0"></div>
                          <div className="sk_line sk_line_height_28 mb-0"></div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          );
        })}
      </div>
    </div>
  );
};

export default React.memo(RecentpostSkeleton);
